import React from 'react';
import Classes from '../css/MyWork.module.scss';
import portfolio_01 from '../assets/images/portfolio-01.jpg';
import portfolio_02 from '../assets/images/portfolio-02.jpg';
import pasta from '../assets/images/pasta1.jpg';
import postman from '../assets/images/postman.jpg';
import volleyball from '../assets/images/volleyball1.jpg';
import { NavLink } from 'react-router-dom';

function MyWork(){
    return (
        <section className={Classes.my_work} id="work">
        <h2 className={Classes.section__title}>My Work</h2>
        <p className={[Classes.section__subtitle, Classes.section__subtitle__work].join(' ')}>A selection of my range of work</p>

        <div className={Classes.porfolio}>
            {/* <a href="https://yatti123.github.io/weather-app/" className={Classes.portfolio__item}> */}
            {/* <a href="/weather" className={Classes.portfolio__item}> */}
            <NavLink to="/weather" className={Classes.portfolio__item}>
                <img src={portfolio_01} alt="" className={Classes.portfolio__img} />
                <div className={Classes.image_txt}>
                    Check Weather
                </div>
            </NavLink>

            {/* <a href="https://yatti123.github.io/recipe-finder-app/" className={Classes.portfolio__item}>
                <img src={portfolio_02} alt="" className={Classes.portfolio__img}/>
                <div className={Classes.image_txt}>
                    Project 2
                </div>
            </a> */}
            <NavLink to="/recipe-finder-app" className={Classes.portfolio__item}>
                <img src={portfolio_02} alt="" className={Classes.portfolio__img}/>
                <div className={Classes.image_txt}>
                    Get your Favorite Recipe
                </div>
            </NavLink>

            <NavLink to="/hunger-burger" className={Classes.portfolio__item}>
                <img src={pasta} alt="" className={Classes.portfolio__img}/>
                <div className={Classes.image_txt}>
                    Order Your Burger
                </div>
            </NavLink>

            <a href="postman.html" className={Classes.portfolio__item}>
                <img src={postman} alt="" className={Classes.portfolio__img}/>
                <div className={Classes.image_txt}>
                    Postman Clone
                </div>
            </a>

            <a href="hobbies.html" className={Classes.portfolio__item}>
                <img src={volleyball} alt="" className={Classes.portfolio__img}/>
                <div className={Classes.image_txt}>
                    Hobbies
                </div>
            </a>
        </div>
    </section>

    )
};

export default MyWork;