import React, { Component } from "react";
import BurgerLayout from "./hoc/Layout/Layout";
import BurgerBuilder from "./containers/BurgerBuilder/BurgerBuilder";
import Checkout from "./containers/Checkout/Checkout";
import Orders from "./containers/Orders/Orders";
import Auth from "./containers/Auth/Auth";
import Logout from "./containers/Auth/Logout/Logout";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
import { connect } from "react-redux";
import * as actions from "./store/actions/index";

class Burger extends Component {
  componentDidMount() {
    this.props.onTryAutoSignup();
  }
  render() {
    let routes = (
      <Switch>
        <Route path="/hunger-burger/auth" component={Auth} />
        <Route path="/hunger-burger" exact component={BurgerBuilder} />
        <Redirect to="/hunger-burger" />
      </Switch>
    );

    if (this.props.isAuthenticated) {
      routes = (
        <Switch>
          <Route path="/hunger-burger/checkout" component={Checkout} />
          <Route path="/hunger-burger/Orders" component={Orders} />
          <Route path="/hunger-burger/logout" component={Logout} />
          <Route path="/hunger-burger" exact component={BurgerBuilder} />
          <Redirect to="/hunger-burger" />
        </Switch>
      );
    }
    return (
      <div>
          {/* <BrowserRouter> */}
          <BurgerLayout>
              {routes}
          </BurgerLayout>
          {/* </BrowserRouter> */}
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    isAuthenticated: state.auth.token !== null,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    onTryAutoSignup: () => dispatch(actions.authCheckState()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Burger);
