import React from 'react';
import Classes from '../css/Academic.module.scss';
import Intro from './Introduction';
import Footer from './Footer';
import booksPic from '../assets/images/books.jpg';

function Academic(){
    return (
        <>
        <Intro 
            sectionTitle='Academic Credentials' 
            picture={booksPic}
            sectionSubtitle='B.Tech(ECE)'/>
        <div>
            <table cellSpacing="0">
                <thead>
                    <tr>
                        <th>S.No</th>
                        <th>Qualification</th>
                        <th>Institution</th>
                        <th>Board/ University</th>
                        <th>Percentage%</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>1.</td>
                        <td>B.Tech. (ECE)</td>
                        <td>KIMT, Moradabad, U.P</td>
                        <td>GBTU</td>
                        <td>78.06%</td>
                    </tr>
                    <tr>
                        <td>2.</td>
                        <td>Class XII</td>
                        <td>G.N.P.S, Moradabad, U.P</td>
                        <td>C.B.S.E</td>
                        <td>71.2%</td>
                    </tr>
                    <tr>
                        <td>3.</td>
                        <td>Class X</td>
                        <td>G.N.P.S, Moradabad, U.P</td>
                        <td>C.B.S.E</td>
                        <td>82.6%</td>
                    </tr>
                </tbody>
            </table>
        </div>
        <Footer />
        </>
    )
};

export default Academic;