import React from 'react';
import Classes from '../../css/Services.module.scss';
import Service from './service';

export function Services(){
    return (
        <section className={Classes.my_services} id="services">
        <h2 className={[Classes.section_title, Classes.section_title__services].join(' ')}>What I do</h2>
        <div className={Classes.services}>
            <Service 
                heading={'Oracle Development'} 
                para={`I am Oracle certified developer having experience as an Oracle PL/SQL Developer in Analysis, Design                     and
                    Implementation of Business Application using the oracle Relational Database Management System(RDBMS).`}/>
            <Service 
                heading={'Front-end Developer'} 
                para={`I like to code things from scratch, and enjoy bringing ideas to life Using HTML, CSS , and Javascript,
                    I build fast, interactive and user friendly websites and web application according to user requirements.`}/>
        </div>
        <a href="#work" className={Classes.btn}>My Work</a>
    </section>
    )
}