import React, { Suspense } from "react";
import ReactDOM from "react-dom";
import App from "./App";
// import Weather from "./Container/Weather/Weather";
// import RecipeFinder from "./Container/RecipeFinder/RecipeFinder";
import Burger from "./Container/Burger/Burger";
import Layout from "./Component/hoc/Layout";
import Academic from "./Container/Academinc";
import Contact from "./Container/Contact";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
import { Provider } from "react-redux";
import { createStore, combineReducers, applyMiddleware } from "redux";
import AuthReducer from "./Container/Burger/store/reducers/auth";
import BurgerReducer from "./Container/Burger/store/reducers/burgerBuilder";
import OrderReducer from "./Container/Burger/store/reducers/order";
import thunk from "redux-thunk";

const Weather = React.lazy(() => import("./Container/Weather/Weather"));
const RecipeFinder = React.lazy(() =>
  import("./Container/RecipeFinder/RecipeFinder")
);

const rootReducer = combineReducers({
  auth: AuthReducer,
  burgerBuilder: BurgerReducer,
  order: OrderReducer,
});

const store = createStore(rootReducer, applyMiddleware(thunk));

ReactDOM.render(
  <Provider store={store}>
    <BrowserRouter>
      <Layout>
        <Switch>
          <Route
            path="/weather"
            render={() => (
              <Suspense fallback={<div>Loading...</div>}>
                <Weather />
              </Suspense>
            )}
          />
          <Route
            path="/recipe-finder-app"
            render={() => (
              <Suspense fallback={<div>Loading...</div>}>
                <RecipeFinder />
              </Suspense>
            )}
          />
          <Route path="/hunger-burger" component={Burger} />
          <Route path="/Academic" component={Academic} />
          <Route path="/Contact" component={Contact} />
          <Route path="/" exact component={App} />
          <Redirect to="/" />
        </Switch>
      </Layout>
    </BrowserRouter>
  </Provider>,
  document.getElementById("root")
);
