import React, { useState } from "react";
// import '../css/variables.scss';
import Classes from "../css/Header.module.scss";
import { NavLink } from 'react-router-dom';

export function Header() {
  const [openNav, setopenNav] = useState(false);

  const toggleNav = () => {
    setopenNav((prev) => !prev);
  };

  const closeNav = () => {
    setopenNav(false);
  }

  const attachedClasses = [Classes.navItem, Classes.downloadResume];

  return (
    <main className={openNav ? Classes.navOpen : null}>
      <header className={Classes.header}>
        <div className={Classes.logo}>Yatender</div>
        <button
          className={Classes.navToggle}
          aria-label="toggle navigation"
          onClick={toggleNav}
        >
          <span className={Classes.hamburger}></span>
        </button>
        <nav className={Classes.nav}>
          <ul className={Classes.navList} onClick={closeNav}>
            <li className={Classes.navItem}>
              {/* <a href="#home" className={Classes.navLink}>
                Home
              </a> */}
              <NavLink to='/' className={Classes.navLink}>Home</NavLink>
            </li>
            <li className={attachedClasses.join(" ")}>
              <a
                href="https://drive.google.com/file/d/1RA7iOMGPbjzSARQydZU63QfW0wy5eeFs/view?usp=sharing"
                className={Classes.navLink}
              >
                Download
              </a>
            </li>
            <li className={Classes.navItem}>
              <a href="#services" className={Classes.navLink}>
                My Services
              </a>
            </li>
            <li className={Classes.navItem}>
              <a href="#about" className={Classes.navLink}>
                About Me
              </a>
            </li>
            <li className={Classes.navItem}>
              <NavLink to='/Academic' className={Classes.navLink}>
                Academic Details
              </NavLink>
            </li>
            <li className={Classes.navItem}>
              {/* <a href="#work" className={Classes.navLink}>
                My Work
              </a> */}
              <NavLink to='/#work' className={Classes.navLink}>My Work</NavLink>
            </li>
            <li className={Classes.navItem}>
              <NavLink to='/Contact' className={Classes.navLink}>
                Contact Me
              </NavLink>
            </li>
          </ul>
        </nav>
      </header>
    </main>
  );
}
