import React from 'react';
import classes from './Navigationitems.module.css';
import NavigationItem from '../Navigationitems/Navigationitem/Navigationitem';

const navigationItems = (props)=>(
    <ul className = {classes.NavigationItems}>
        <NavigationItem link="/hunger-burger" active>Burger Builder</NavigationItem>
        {props.isAuthenticated ? <NavigationItem link="/hunger-burger/Orders">Orders</NavigationItem> : null}
        {!props.isAuthenticated 
            ? <NavigationItem link="/hunger-burger/auth">Authenticate</NavigationItem> 
            : <NavigationItem link="/hunger-burger/logout">Logout</NavigationItem>}
    </ul>
)


export default navigationItems