import React from 'react'
import BurgerIngredient from './BurgerIngredients/BurgerIngredients'
import styled from 'styled-components'

const StyledDiv = styled.div`
    width: 100%;
    margin: auto;
    height: 250px;
    overflow: auto;
    text-align: center;
    font-weight: bold;
    font-size: 1.2rem;

    @media (min-width: 500px) and (max-height: 400px)  {
        width: 350px;
        height: 300px;
    }

    @media (min-width: 500px) and (min-height: 401px)  {
        width: 450px;
        height: 400px;
    }

    @media (min-width: 1000px) and (min-height: 700px)  {
        width: 700px;
        height: 600px;
        background-color : #dfdfdf;
    }
`;

const burger = (props)=>{
    // Convert Ingredients object into Ingredient array
    // Outer map will return an array of Arrays. Inner Arrays items are 
    //ingredients repeating with their exact counts

    // Simple solution is in Order.js file
    let transformedIngredients = Object.keys(props.ingredients)
    .map(igKey => [...Array(props.ingredients[igKey])].map((_,i) => {
        return <BurgerIngredient key = {igKey+i} type={igKey} />
    })).reduce((arr, el) => {
        return arr.concat(el)
    }, []);
    
    if(transformedIngredients.length === 0){
        transformedIngredients = <p>Please start adding ingredients!</p>
    }

    return (
            <StyledDiv>
                <BurgerIngredient type='bread-top' />
                {transformedIngredients}
                <BurgerIngredient type='bread-bottom' />
            </StyledDiv>
    );
}

export default burger;