import React from 'react';
import Classes from '../css/Introduction.module.scss';

function Introduction(props){
    return (
        <section className={Classes.intro} id="home">
            <h1 className={[Classes.section__title, Classes.section__title__intro].join(' ')}>
                {props.sectionTitle}<strong>{props.myName}</strong>
            </h1>
            <div className={Classes.box}>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
            </div>
            <p className={[Classes.section__subtitle, Classes.section__subtitle__intro].join(' ')}>
                {props.sectionSubtitleFont? <i class="fas fa-envelope-open-text"></i>: null} + {props.sectionSubtitle}
            </p>
            <img src={props.picture} alt="yatti smiling" className={Classes.intro__img}/>
            <a href="https://drive.google.com/file/d/1RA7iOMGPbjzSARQydZU63QfW0wy5eeFs/view?usp=sharing"
                className={Classes.section__subtitle__download}
                title="Download My Resume">
                <i className="fas fa-cloud-download-alt fa-2x"></i>
            </a>
        </section>
    )
}

export default Introduction