import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from '../../store/actions/index';
import Aux from '../../hoc/_ux/_ux';
import Burger from '../../components/Burger/Burger';
import BuildControls from '../../components/Burger/BuildControls/BuildControls';
import Modal from '../../components/UI/Modal/Modal';
import OrderSummary from '../../components/Burger/OrderSummary/orderSummary';
import axios from '../../../../axios-orders';
import Spinner from '../../components/UI/Spinner/Spinner';
import withErrorHandler from '../../hoc/withErrorHandler/withErrorHandler';

export class BurgerBuilder extends Component  {
    // constructor(props){
    //     super(props);
    //     this.state = {...}
    // }

    state = {
        orderSummary : false
    }

    componentDidMount(){
        // axios.get('https://react-my-burger-a88bf-default-rtdb.firebaseio.com/ingredients.json')
        //     .then(response=>{
        //         this.setState({ingredients : response.data})                
        //     })
        //     .catch(error=>{
        //         this.setState({ error : true });
        //     })
        this.props.onInitIngredients();
    }

    updatePurchaseState (newIngredients) {
        const ingredients = {
            ...newIngredients
        };
        const sum = Object.keys(ingredients).map(igKey => ingredients[igKey])
            .reduce((sum, el) => {
                return sum + el;
            }, 0);
        return sum > 0;
    }

    
    purchaseHandler = (event)=>{
        if(this.props.isAuthenticated){
            this.setState ({
                orderSummary : true
            })
        }
        else {
            this.props.onSetAuthRedirectPath('/hunger-burger/checkout');
            this.props.history.push('/hunger-burger/auth');
        }
    }

    purchaseCancleHandler = ()=>{
        this.setState ({
            orderSummary : false
        })
    }

    //If we don't use redux
    // purchaseContinueHandler = ()=>{
    //     // alert('You Continue!');
    //        //These query params will be recieved in the component which is being called by this route
    //        // Here we are passing props to the siblings(checkout).
    //         //Parent of BurgeBuilder and Checkout is App.js

    //        const queryParams = [];

    //        for(let i in this.props.ings) {
    //         queryParams.push(encodeURIComponent(i) + '=' + encodeURIComponent(this.props.ings[i]));
    //        }

    //        queryParams.push('price=' + this.props.totPrice);

    //        const queryString = queryParams.join('&');
    //         this.props.history.push({
    //             pathname : '/checkout',
    //             // ingredients : {...this.state.ingredients}
    //             search : '?' + queryString
    //         });
    // }

    purchaseContinueHandler = ()=>{
            this.props.onInitPurchase();
            this.props.history.push('/hunger-burger/checkout');
    }
    
    render(){
        const disabledInfo = {
            ...this.props.ings
        }

        for(let key in disabledInfo){
            disabledInfo[key] = disabledInfo[key] <= 0
        }
        // disabledInfo = {salad: true, meat: false, ...}
        let orderSummary = null;
        let burger = this.props.error ? <p>Ingredients can't be loaded</p> : <Spinner />;

        if(this.props.ings){
            burger = (
                <Aux>
                    <Burger ingredients={this.props.ings}/>
                    <BuildControls
                        addIngredients={this.props.onIngredientAdded}
                        removeIngredients={this.props.onIngredientRemoved}
                        disabled = {disabledInfo}
                        price = {this.props.totPrice}
                        purchaseable = {this.updatePurchaseState(this.props.ings)}
                        displayOrderSummary = {this.purchaseHandler}
                        isAuth = {this.props.isAuthenticated}/>
                </Aux>
            );
            orderSummary = <OrderSummary 
                                purchaseCanceled={this.purchaseCancleHandler}
                                purchaseContinued={this.purchaseContinueHandler}
                                ingredients={this.props.ings}
                                total = {this.props.totPrice}
                            />;
        }
        
        return (
            <Aux>
                <Modal show={this.state.orderSummary} modalClosed={this.purchaseCancleHandler}>
                    {orderSummary}
                </Modal>
                {burger}
            </Aux>
        )
    }

}

const mapStateToProps = state => {
    return {
        ings: state.burgerBuilder.ingredients,
        totPrice: state.burgerBuilder.totalPrice,
        error: state.burgerBuilder.error,
        isAuthenticated: state.auth.token !== null
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onIngredientAdded: (ingType) => dispatch(actions.addIngredient(ingType)),
        onIngredientRemoved: (ingType) => dispatch(actions.removeIngredient(ingType)),
        onInitIngredients: () => dispatch(actions.initIngredients()),
        onInitPurchase: () => dispatch(actions.purchaseInit()),
        onSetAuthRedirectPath: (path) => dispatch(actions.setAuthRedirect(path))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withErrorHandler(BurgerBuilder, axios));