import * as actionTypes from '../actions/actionTypes';
import { updateObject } from '../utility';

const initialState = {
    ingredients : null,
    totalPrice : 4,
    error : false,
    building: false
}

//Global variables should be in upper case
const INGREDIENT_PRICES = {
    salad : 0.5,
    cheese : 2,
    meat : 2,
    bacon : 1
}

const addIngredients = (state, action) => {
    const updatedIngredient = {[action.ingredientType]: state.ingredients[action.ingredientType] + 1};
    const updatedIngredients = updateObject(state.ingredients, updatedIngredient);
    const updatedState = {
        ingredients: {...updatedIngredients},
        totalPrice: state.totalPrice + INGREDIENT_PRICES[action.ingredientType],
        building: true
    }
    return updateObject(state, updatedState);
}

const removeIngredients = (state, action) => {
    const updatedIng = {[action.ingredientType]: state.ingredients[action.ingredientType] - 1};
    const updatedIngs = updateObject(state.ingredients, updatedIng);
    const updatedSt = {
        ingredients: {...updatedIngs},
        totalPrice: state.totalPrice + INGREDIENT_PRICES[action.ingredientType],
        building: true
    }
    return updateObject(state, updatedSt);
};

// setIngredients - just loaded the page and set the state from initial values from server
const setIngredients = (state, action) => {
    return updateObject(state, {
        ingredients: {
            salad: action.ingredients.salad,
            bacon: action.ingredients.bacon,
            cheese: action.ingredients.cheese,
            meat: action.ingredients.meat
        },
        error: false,
        totalPrice: 4,
        building: false
    });
};

const fecthIngredientsFailed = (state, action) => {
    return updateObject(state, {error: true});
};

const reducer = (state = initialState, action) => {
    switch (action.type){
        case actionTypes.ADD_INGREDIENT: return addIngredients(state, action);

        case actionTypes.REMOVE_INGREDIENT: return removeIngredients(state, action);

        case actionTypes.SET_INGREDIENTS: return setIngredients(state, action);         

        case actionTypes.FETCH_INGREDIENTS_FAILED: return fecthIngredientsFailed(state, action);
        
        default: return state
        }

}

export default reducer;