// import axios from 'axios';
import * as actionTypes from './actionTypes';
import axios from '../../../../axios-orders';

export const authStart = () => {
    return {
        type: actionTypes.AUTH_START
    };
};

export const authSuccess = (token, userId) => {
    return {
        type: actionTypes.AUTH_SUCCESS,
        idToken: token,
        userId: userId
    };
};


export const authFail = (error) => {
    return {
        type: actionTypes.AUTH_FAIL,
        error: error
    };
};

export const logout = ()=>{
    localStorage.removeItem('token');
    localStorage.removeItem('expirationDate');
    localStorage.removeItem('userId');
    return {
        type: actionTypes.AUTH_LOGOUT
    }
};


export const checkAuthTimeout = (expirationTime) => {
    return dispatch => {
        setTimeout(()=>{
            dispatch(logout());
        },expirationTime) 
    };
};


export const auth = (email, password, isSignup) => {
    return dispatch => {
        dispatch(authStart());
        const authData = {
            email: email,
            password: password,
            returnSecureToken: true
        }
        let url = 'https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyC97oM47oAWOrEdBDXnwpeHNtTP86bOaUQ';

        if(!isSignup){
            url = 'https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyC97oM47oAWOrEdBDXnwpeHNtTP86bOaUQ';
        }
        axios.post(url,authData)
        .then(response => {
            // console.log(response);
            const expirationDate = new Date(new Date().getTime() + response.data.expiresIn *1000);
            window.localStorage.setItem('token', response.data.idToken);
            window.localStorage.setItem('expirationDate', expirationDate);
            window.localStorage.setItem('userId', response.data.localId);
            dispatch(authSuccess(response.data.idToken, response.data.localId));
            dispatch(checkAuthTimeout(response.data.expiresIn * 1000)); // convert into milliseconds
        })
        .catch(err => {
            // console.log(err.response);
            dispatch(authFail(err.response.data.error));
        })
    };
};


export const setAuthRedirect = (path)=>{
    return {
        type: actionTypes.SET_AUTH_REDIRECT_PATH,
        path: path
    }
};

export const authCheckState = () => {
    return dispatch => {
        const token = localStorage.getItem('token'); 
        if(!token){
            return dispatch(logout()); // not require as such
        } else {
            console.log('token still present...');
            // Convert date string returned from local storage into date
            const expirationDate = new Date(localStorage.getItem('expirationDate')); 

            
            if(expirationDate <= new Date()){
                console.log('logging out');
                dispatch(logout());
            } else {
                console.log('signing in');
                const userId = localStorage.getItem('userId');
                dispatch(authSuccess(token, userId));
                dispatch(checkAuthTimeout(expirationDate.getTime() - new Date().getTime()));
                //The getTime() method returns the number of milliseconds between midnight of January 1, 1970 and the specified date.
            }

            
        }
    }
};